@import "variables";
@import "mixins";

// use examples1 -------------- using variables
@include media($minw, $xs) {
  p {
    font-size: 1em;
    padding: 0px; } }

@include media($minw, $xs, $maxw, $lg) {
  p {
    font-size: 1em;
    padding: 0px; } }

// use examples2 -------------- using both varible and string
@include media($minw, "1000px") {
  p {
    font-size: 2em;
    padding: 10px; } }

// use examples3 -------------- using strings only
@include media("min-width", $mobile ,"max-width", "62.5em") {
  p {
    font-size: 3em;
    padding: 15px; } }

// use examples4 -------------- using predefind configuration
@include media($tablet) {
  p {
    font-size: 4em;
    padding: 20px; } }
